import React from 'react';
import { Button, Card, Select } from 'antd';

const Option = Select.Option;

const CourseCard = props => {
  if (!props.visible) {
    return null;
  }

  const handleGPAChange = (value) => {
    props.updateCourseGPA(props.index, value);
  };

  const handleCreditsChange = (value) => {
    props.updateCourseCredits(props.index, value);
  };

  const handleCodeChange = (value) => {
    props.updateCourseCode(props.index, value);
  };  
  const handleCourseChange = (value) => {
    props.updateCourseCode(props.index, value);
  };  


  return (
    <Card
      style={{ marginTop: 18 }}
      type="inner"
      title={`Subject ${props.realIndex}`}
      extra={<Button onClick={() => props.deleteCourse(props.index)} type="danger" icon="delete"></Button>}
    >
      
      <Select
        style={{ width: 200 }}
        placeholder="Code"
        optionFilterProp="children"
        filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
        onChange={handleCodeChange}
      >
        <Option value={0}>CSX1001 Computer Programming</Option>
        <Option value={1}>CSX2001</Option>
        <Option value={2}>ITX3002</Option>
        <Option value={3}>GEX4001</Option>

       
      </Select>

      <Select
        style={{ width: 80 }}
        placeholder="Grade"
        optionFilterProp="children"
        filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
        className="inputCredits"
        onChange={handleGPAChange}
      >
        <Option value={4.0}>A</Option>
        <Option value={3.75}>A-</Option>
        <Option value={3.25}>B+</Option>
        <Option value={3.0}>B</Option>
        <Option value={2.75}>B-</Option>
        <Option value={2.25}>C+</Option>
        <Option value={2.0}>C</Option>
        <Option value={1.75}>C-</Option>
        <Option value={1.0}>D</Option>
        <Option value={0.00}>F</Option>
        <Option value={null}>W,I,S,U,R,TR</Option>

      </Select>

      <Select
        style={{ width: 80 }}
        placeholder="Credits"
        optionFilterProp="children"
        filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
        className="inputCredits"
        onChange={handleCreditsChange}
      >
        <Option value={0}>0</Option>
        <Option value={1}>1</Option>
        <Option value={2}>2</Option>
        <Option value={3}>3</Option>

      </Select>

      <Select
        style={{ width: 100 }}
        placeholder="Course"
        optionFilterProp="children"
        filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
        className="inputCredits"
        onChange={handleCourseChange}
      >
        <Option value={0}>CSX1001</Option>
        <Option value={1}>CSX2001</Option>
        <Option value={2}>ITX3002</Option>
        <Option value={3}>GEX4001</Option>

      </Select>
    </Card>
  );
}

export default CourseCard;